@charset "UTF-8";
@import 'https://fonts.googleapis.com/css2?family=Libre+Bodoni:ital@0;1&display=swap';
@import 'https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css';
@import 'https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.2.0/css/all.min.css';
* {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  --kb-vh: 1vh;
  --kb-font-family-display: 'Libre Bodoni', serif;
  --kb-font-family-body: 'HelveticaNeue-Light', 'Helvetica Neue Light', 'Helvetica Neue', Helvetica, Arial, 'Lucida Grande', sans-serif;
  --kb-color-brand: #f68b1f;
  --kb-color-text-inverse: white;
  --kb-color-text-default: #1e1e1e;
  --kb-color-text: var(--kb-color-text-inverse);
  --kb-color-text-link: var(--kb-color-brand);
  --kb-color-text-link-hover: #d8791a;
  --kb-color-background-dark: #1e1e1e;
  --kb-color-background-light: #f8f6f6;
  --kb-color-background: var(--kb-color-background-dark);
  --kb-color-project-background-dark: rgba(0, 0, 0, 0.8);
  --kb-color-project-background-light: rgba(255, 255, 255, 0.825);
  --kb-color-project-background: var(--kb-color-project-background-dark);
  --kb-color-carousel-nav-background-dark: rgba(0, 0, 0, 0.2);
  --kb-color-carousel-nav-background-light: rgba(255, 255, 255, 0.2);
  --kb-color-carousel-nav-background: var(--kb-color-carousel-nav-background-dark);
  --kb-spacing-xx-small: 0.25rem;
  --kb-spacing-x-small: 0.5rem;
  --kb-spacing-small: 0.75rem;
  --kb-spacing-medium: 1rem;
  --kb-spacing-large: 1.5rem;
  --kb-spacing-x-large: 2rem;
  --kb-spacing-xx-large: 3rem;
  --kb-shadow--focus: 0 0 4px var(--kb-color-brand);
  min-height: calc(var(--kb-vh) * 100);
  margin: 0;
  background-color: var(--kb-color-background);
  color: var(--kb-color-text);
  font-family: var(--kb-font-family-body);
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.75;
}

@media (prefers-color-scheme: light) {
  body {
    --kb-color-text: var(--kb-color-text-default);
    --kb-color-background: var(--kb-color-background-light);
    --kb-color-project-background: var(--kb-color-project-background-light);
    --kb-color-carousel-nav-background: var(--kb-color-carousel-nav-background-light);
  }
}

@media (prefers-color-scheme: dark) {
  body {
    --kb-color-text: var(--kb-color-text-inverse);
    --kb-color-background: var(--kb-color-background-dark);
    --kb-color-project-background: var(--kb-color-project-background-dark);
    --kb-color-carousel-nav-background: var(--kb-color-carousel-nav-background-dark);
  }
}

a {
  color: var(--kb-color-text-link);
}

a:hover {
  color: var(--kb-color-text-link-hover);
}

.kb-logo {
  display: block;
  width: 10rem;
}

@media (prefers-color-scheme: dark) {
  .kb-logo {
    display: none;
  }
}

.kb-logo--inverse {
  display: block;
  width: 10rem;
}

@media (prefers-color-scheme: light) {
  .kb-logo--inverse {
    display: none;
  }
}

.kb-hero {
  margin-top: calc(var(--kb-hero-height) * -1);
  opacity: 0;
}

.kb-hero--loaded {
  margin-top: 0;
  opacity: 1;
  transition: margin-top 500ms ease 500ms, opacity 500ms ease 750ms;
}

.kb-hero__layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 8rem;
}

@media (max-width: 1280px) {
  .kb-hero__layout {
    padding: 6rem;
  }
}

.kb-hero__content {
  flex: 0 0 auto;
}

.kb-hero__description {
  margin-top: 2rem;
  max-width: 27.5rem;
}

.kb-hero__sidebar {
  flex: 0 0 auto;
  padding-top: 6.75rem;
}

.kb-hero__link {
  display: block;
  position: relative;
  color: var(--kb-color-text);
  text-decoration: none;
}

.kb-hero__link-list {
  list-style: none;
  padding-left: 2.75rem;
  display: flex;
  margin: 1.25rem 0 0;
}

.kb-hero__link a {
  display: block;
  padding: 0.5rem 0.525rem;
  border-radius: 0.25rem;
  color: var(--kb-color-text);
  text-decoration: none;
  line-height: 1;
}

.kb-hero__link a:hover, .kb-hero__link a:focus, .kb-hero__link a:active {
  color: var(--kb-color-brand);
}

.kb-hero__link a:focus, .kb-hero__link a:active {
  outline: none;
  box-shadow: var(--kb-shadow--focus);
}

.kb-hero__link-label {
  display: block;
  font-size: 0.8rem;
  color: var(--kb-color-brand);
  pointer-events: none;
  position: absolute;
  top: calc(100% - 0.125rem);
  left: 50%;
  transform: translateX(-50%) translateY(-0.5rem);
  opacity: 0;
  transition: all 250ms ease;
}

.kb-hero__link > a:hover + .kb-hero__link-label {
  opacity: 1;
  transform: translateX(-50%);
}

.kb-hero__controls {
  padding: 0 0 0 3rem;
  margin-top: 4.3rem;
  border: none;
}

.kb-gallery {
  margin-bottom: 4rem;
}

.kb-project {
  flex: 0 0 20%;
  overflow: hidden;
}

@media (max-width: 1440px) {
  .kb-project {
    flex-basis: 25%;
  }
}

.kb-project-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  pointer-events: none;
}

.kb-project-list--loaded {
  pointer-events: auto;
}

.kb-project__link {
  display: block;
  position: relative;
  padding-bottom: 66.666%;
}

.kb-project__thumbnail {
  position: absolute;
  inset: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  margin-top: 100%;
  opacity: 0;
  transition: all 250ms ease;
}

.kb-project--loaded .kb-project__thumbnail,
.kb-project--revealed .kb-project__thumbnail {
  margin-top: 0;
  opacity: 1;
}

.kb-project--revealed .kb-project__thumbnail {
  transition-property: transform, filter;
}

.kb-project__link:hover .kb-project__thumbnail,
.kb-project__link:focus .kb-project__thumbnail {
  transform: scale(1.05);
  filter: blur(5px) saturate(0.75);
}

.kb-project__hover-tile {
  position: absolute;
  inset: 0;
  padding: 15% 35% 15% 15%;
  opacity: 0;
  transition: opacity 250ms ease;
  background-color: var(--kb-color-project-background);
}

.kb-project__link:hover .kb-project__hover-tile,
.kb-project__link:focus .kb-project__hover-tile {
  opacity: 1;
}

.kb-project__name, .kb-project__year {
  display: block;
  position: relative;
  color: var(--kb-color-text);
  transform: translateX(-1rem);
  transition: all 250ms ease 150ms;
  opacity: 0;
}

.kb-project__link:hover .kb-project__name,
.kb-project__link:focus .kb-project__name, .kb-project__link:hover .kb-project__year,
.kb-project__link:focus .kb-project__year {
  transform: none;
  opacity: 1;
}

.kb-project__name {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 1.25;
  color: var(--kb-color-brand);
  transition-delay: 100ms;
}

.kb-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 4rem 4rem;
  opacity: 0;
  transition: opacity 500ms ease 750ms;
}

.kb-footer--loaded {
  opacity: 1;
}

.kb-checkbox__input {
  display: block;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  pointer-events: auto;
}

.kb-checkbox__faux {
  display: block;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 0.25rem;
  border: thin solid rgba(136, 136, 136, 0.5);
  position: relative;
}

.kb-checkbox__input:focus + .kb-checkbox__layout .kb-checkbox__faux,
.kb-checkbox__input:active + .kb-checkbox__layout .kb-checkbox__faux {
  box-shadow: var(--kb-shadow--focus);
}

.kb-checkbox__faux::before {
  display: none;
  content: '';
  height: 0.25rem;
  width: 0.5rem;
  position: absolute;
  top: 42.5%;
  left: 50%;
  transform-origin: center;
  transform: translate3d(-50%, -50%, 0) rotate(-45deg);
  border-left: 2px solid var(--kb-color-brand);
  border-bottom: 2px solid var(--kb-color-brand);
}

.kb-checkbox__input:checked + .kb-checkbox__layout .kb-checkbox__faux::before {
  display: block;
}

.kb-checkbox__layout {
  display: flex;
}

.kb-checkbox__label {
  display: block;
  line-height: 1;
  margin-left: 0.375rem;
  padding-top: 0.25rem;
}

.kb-project-modal {
  position: fixed;
  inset: 0;
  z-index: 9000;
  opacity: 0;
  transition: opacity 250ms ease;
}

.kb-project-modal--shown {
  opacity: 1;
}

.kb-project-modal__backdrop {
  position: absolute;
  pointer-events: none;
  overflow: hidden;
  opacity: 0;
  transition: all 250ms ease, opacity 50ms ease;
}

.kb-project-modal__backdrop::after {
  content: '';
  display: block;
  position: absolute;
  inset: 0;
  background-color: var(--kb-color-project-background);
}

.kb-project-modal__background {
  display: block;
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: scale(1.25);
  filter: blur(15px) saturate(0.75);
}

.kb-project-modal__content {
  height: 100%;
  overflow-y: auto;
  padding: 4rem;
  z-index: 1;
  opacity: 0;
  transition: opacity 500ms ease 350ms;
}

.kb-project-modal__content--shown {
  opacity: 1;
}

.kb-project-modal__close {
  display: block;
  cursor: pointer;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  color: var(--kb-tex-color);
  font-size: 2.5rem;
  line-height: 0;
  width: 3rem;
  height: 3rem;
  padding: 0;
  border: none;
  border-radius: 0.25rem;
  position: absolute;
  top: 0.3rem;
  right: -0.75rem;
}

.kb-project-modal__close:hover, .kb-project-modal__close:focus, .kb-project-modal__close:active {
  color: var(--kb-color-brand);
}

.kb-project-modal__close:focus, .kb-project-modal__close:active {
  outline: none;
  box-shadow: var(--kb-shadow--focus);
}

.kb-project-modal__name {
  display: block;
  padding-right: 4rem;
  line-height: 1.25;
  margin: -0.625rem 0 0 0;
}

.kb-project-modal__detail {
  flex: 0 0 auto;
  padding-right: 2rem;
  margin-top: 1.5rem;
}

.kb-project-modal__detail-list {
  display: flex;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
}

.kb-carousel {
  width: 100%;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.kb-carousel::-webkit-scrollbar {
  display: none;
}

.kb-carousel__container {
  position: relative;
}

.kb-carousel__rail {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: none;
}

.kb-carousel__video-container {
  position: relative;
  padding-top: 56.25%;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  height: auto;
}

.kb-carousel__video-container iframe {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
}

.kb-carousel__image {
  display: block;
  flex: 0 0 100%;
  scroll-snap-align: start;
}

.kb-carousel__back, .kb-carousel__next {
  display: block;
  cursor: pointer;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  color: var(--kb-tex-color);
  font-size: 2.5rem;
  line-height: 0;
  width: 3rem;
  height: 3rem;
  padding: 0;
  border: none;
  border-radius: 0.25rem;
  position: absolute;
  z-index: 1;
  left: -3.5rem;
  top: 50%;
  transform: translateY(-50%);
}

.kb-carousel__back::before, .kb-carousel__next::before {
  display: block;
  transform: translateX(0.125rem);
}

.kb-carousel__back:hover, .kb-carousel__back:active, .kb-carousel__next:hover, .kb-carousel__next:active {
  color: var(--kb-color-brand);
}

.kb-carousel__back:focus, .kb-carousel__back:active, .kb-carousel__next:focus, .kb-carousel__next:active {
  outline: none;
  box-shadow: var(--kb-shadow--focus);
}

.kb-carousel__next {
  left: initial;
  right: -3.5rem;
}

.kb-carousel__next::before {
  transform: translateX(-0.125rem);
}

.kb-carousel__nav {
  display: flex;
  padding: 0.5rem;
  border-radius: 0.75rem;
  position: absolute;
  left: 1.125rem;
  top: 1.125rem;
  background-color: var(--kb-color-carousel-nav-background);
}

.kb-carousel__nav-item {
  display: block;
  cursor: pointer;
  margin: 0 0.25rem;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 0.5rem;
  background-color: var(--kb-color-text);
}

.kb-carousel__nav-item:hover {
  transform: scale(1.25);
}

.kb-carousel__nav-item:first-child {
  margin-left: 0;
}

.kb-carousel__nav-item:last-child {
  margin-right: 0;
}

.kb-carousel__nav-item--active {
  background-color: var(--kb-color-brand);
}

